.header__container {
  position: relative;
  display: flex;
  width: 60px;
  height: 30px;
  border-radius: 30px;
  background: #222;
  cursor: pointer;
  box-shadow: 0 8px 6px rgba(0, 0, 0, 0.1),
    inset 0 8px 8px rgba(0, 0, 0, 0.1),
    inset 0 -4px 4px rgba(0, 0, 0, 0.1);
  transition: 0.2s;
}

@media screen and (max-width:917px) {
  .header__container {
    margin-right: 27px;
  }
}