body {
  height: 100vh;
}

:root {
  --bg-color: #000;
  --text-color: #fff;
  --bg-image-logo: url('./images/header-logo.svg');
  --bg-image-edit: url('./images/Edit__Button.svg');
  --bg-image-add: url('./images/Add-Button.svg');
  --bg-color-group: #fff;
}

[lightmode] {
  --bg-color: #fff;
  --text-color: #000;
  --bg-image-logo: url('./images/header-logo-black.svg');
  --bg-image-edit: url('./images/Edit__Button-black.svg');
  --bg-image-add: url('./images/Add-Button-black.svg');
  --bg-color-group: #c0c0c0;
}

